import React from 'react'
import styled from 'styled-components'
import { Link as LinkR } from 'react-router-dom'
import { Link as LinkS } from 'react-scroll'

const Nav = styled.nav`
  width: 95%;
  margin: 0 auto;
  padding-top: 30px;

  @media(max-width: 768px){
      transition: 0.8s all ease;
  }
`

const NavbarContainer = styled.div`
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
    -ms-flex-pack: justify;
        justify-content: space-between;
  align-items: center;
`

const NavLogo = styled(LinkR)`
  cursor: pointer;
`

const MenuBurger = styled.button`
  outline: none;
  border: none;
  background-color: transparent;
  
  &&:hover{
    cursor: pointer;
  }

  @media(min-width: 768px){
    display: none !important;
  }
` 

const MenuHeader = styled.article`
  display: none ;

  @media(min-width: 768px){
  width: auto;
  height: auto;
  display: block !important;
  background-color: transparent;
  position: inherit;
}
`

const NavLink = styled(LinkS)`
    text-decoration: none;
    color: #818498;
    font-size: 1.1em;
    color: white;
    padding: 0 20px;
    cursor: pointer;

    &&:hover{
      color: #fad400;
    }
`

const NavBtn = styled.button`
    outline: none;
    border: none;
    background-color: white;
    padding: 15px 25px;
    border-radius: 25px;
    text-transform: uppercase;
    font-family: 'Fraunces', serif;
    cursor: pointer;
    transition: 250ms ease-in-out;
    
    &&:hover{
      background-color: rgb(135, 205, 252);
      color: white;
    }
`


const NavbarEl = ({ toggle }) => {
        return (
            <>
                <Nav>
                    <NavbarContainer>
                        <NavLogo to="/"><img src="../../icon/logo.svg" alt="sunnyside" /></NavLogo>
                        <MenuBurger onClick={toggle}><img src="../../icon/icon-hamburger.svg" alt="menu" /></MenuBurger>
                        <MenuHeader>
                            <NavLink to="about">About</NavLink>
                            <NavLink to="services">Services</NavLink>
                            <NavLink to="projects">Projects</NavLink>
                            <NavBtn>Contact</NavBtn>
                        </MenuHeader>
                    </NavbarContainer>
                </Nav>
            </>
        )
}


export default NavbarEl
