import React from 'react'
import styled from 'styled-components'
import { Link as LinkS } from 'react-scroll'

const SidebarContainer = styled.aside`
width: auto;
  height: 300px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: space-evenly;
      -ms-flex-pack: space-evenly;
          justify-content: space-evenly;
  background-color: white;
  position: absolute;
  top: 120px;
  left: 10%;
  right: 10%;

  transition: 0.3s ease-in-out;
  opacity: ${({ isOpen }) => (isOpen ? '100%' : '0')};
  top: ${({ isOpen }) => (isOpen ? '0' : '-100%')};
  transform: translateY(50%);
  z-index: 10;

  @media(min-width: 768px){
      display: none !important;
  }
`


const SideNavLink = styled(LinkS)`
    text-decoration: none;
    color: #818498;
    font-size: 1.1em;
    cursor: pointer;

    &&:hover{
        color: #fad400;
        border-bottom: 2px dashed #fad400;
    }
`

const SideNavBtn = styled.button`
    outline: none;
    border: none;
    background-color: #fad400;
    padding: 15px 25px;
    border-radius: 25px;
    text-transform: uppercase;
    font-family: 'Fraunces', serif;
    cursor: pointer;

    &&:hover{
        background-color: transparent;
        border: 1px dashed black;
    }

    @media(min-width: 768px){
      background-color: white;
      -webkit-transition: 250ms;
      transition: ease 250ms;
    
        &&:hover{
         background-color: transparent;
         -webkit-transition: 250ms;
         transition: 250ms ease-in-out;
        }
    }
`


const Sidebar = ({isOpen, toggle}) => {
    return (
        <SidebarContainer isOpen={isOpen} onClick={toggle}>
                <SideNavLink to="about">About</SideNavLink>
                <SideNavLink to="services">Services</SideNavLink>
                <SideNavLink to="projects">Projects</SideNavLink>
                <SideNavBtn>Contact</SideNavBtn>
        </SidebarContainer>
    )
}

export default Sidebar
